body{
  font-family: 'Poppins', sans-serif;
}

.wrapper{
  background-color: #eee;
  height: 100vh;
}

.sidebar-wrapper{
  width: 225px;
  height: 100%;
  float: left;
  background: #fff url('./assets/1907.png') 0px 310px no-repeat;
  box-shadow: 0 2px 11px #B6B6B6;

}

.content-wrapper{
  width: calc(100% - 325px);
  padding: 50px;
  float: right;
  height: calc(100vh - 100px);
  overflow-y: auto;
}

.side-content{
  padding: 20px;
  text-align: center;
}

.logo{
  width: 100%;
  margin-bottom: 42px;
}

nav{
  text-align: left;
}

nav li{
  margin-bottom: 36px;
  color: #676767;
}

nav li a{
  color: inherit;
  font-size: 16px;
  text-decoration: none;
  display: block;
}

nav li a.active{
  color: #00BEDA;
}

nav li a:hover{
  color: #00BEDA;
}

nav li img{
  margin-right: 8px;
}

.navbar-icon.show-on-active{
  display: none;
}

.active > .navbar-icon{
  display: none;
}

.active > .navbar-icon.show-on-active{
  display: inline;
}

.card{
  background-color: #FFFFFF;
  box-shadow: 0 2px 11px #B6B6B6;
  padding: 20px;
}

.card>h1{
  text-align: center;
  font-weight: 300;
  color: #737373;
  font-size: 26px;
  margin-top: 0px;
  margin-bottom: 44px;
}

.d-flex{
  display: flex;
}

.justify-content-center{
  justify-content: center;
}

.align-items-center{
  align-items: center;
}

.flex-wrap{
  flex-wrap: wrap;
}

.full-height{
  height: 100%;
}

.add-form{
  padding: 44px 112px;
  width: 379px;
} 

hr{
  box-shadow: none;
  border: none;
  height: 1px;
  background-color: #CFCFCF;
  margin: 0;
}

.form-container{
  padding-top: 56px;
  width: 100%;
}

.add-form label{
  font-size: 15px;
  color: #737373;
  font-weight: 600px;
  display: block;
  margin-bottom: 10px;
}

input[type="text"], input[type="number"]{
  background-color: #F6F6F6;
  border: none;
  height: 40px;
  font-size: 16px;
  color: #666;
  width: 100%;
}

.add-form input{
  margin-bottom: 32px;
}

.add-form input[type="submit"]{
  background-color: #00BEDA;
  color: #fff;
  font-weight: 600;
  width: 100%;
  padding: 16px;
  text-transform: uppercase;
  font-size: 16px;
}

.book-card{
  min-width: 280px;
  margin-right: 50px;
  margin-bottom: 50px;
}

.pull-right{
  float: right;
}

.pull-left{
  float: left;
}

.banner{
  background-color: #F2F9FB;
  text-align: center;
  margin-bottom: 20px;
  margin-top: 20px;
  padding-top: 44px;
  padding-bottom: 44px;
}

.price{
  font-weight: 500;
  color: #505050;
  font-size: 17px;
}

.name{
  color: #00A6BE;
  margin-bottom: 12px;
}

.list-heading{
  color: #737373;
  font-weight: 600;
  font-size: 15px;
  margin-bottom: 30px;
}

.text-right{
  text-align: right;
}

.count{
  padding: 2px;
  background-color: #17C0D9;
  color: #fff;
  float: right;
  font-size: 13px;
  border-radius: 50%;
  height: 18px;
  width: 18px;
  line-height: 18px;
  text-align: center;
}

.relative{
  position: relative;
}

.error{
  position: absolute;
  top: 0;
  right: 0;
  color: red;
  color: 10px;
  display: inline-block;
  margin: 0;
}

@media screen and (max-width: 720px){
  .sidebar-wrapper {
    width: 100%;
    height: 50px;
    box-shadow: 0 2px 11px #b6b6b6;
    position: fixed;
    bottom: 0;
  }

  .side-content {
    padding: 5px;
    text-align: left;
  }

  nav{
    float: right;
  }

  nav ul li{
    display: inline-block;
    margin: 10px 20px 10px 0;
  }

  nav li a{
    font-size: 12px;
  }

  .navbar-icon, .navbar-icon.show-on-active{
    display: none !important;
  }

  .logo{
    width: 100px;
  }

  .count{
    display: none;
  }

  .content-wrapper{
    width: 100%;
    padding: 0;
    clear: both;
    height: auto;
    overflow: auto;
  }

  .add-form{
    padding: 50px;
  }

  .list-heading{
    text-align: center;
    margin-top: 20px;
  }

  .book-card{
    margin: 20px auto;
  }
}